@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;600&family=Source+Serif+Pro:ital,wght@1,300&display=swap);
/*******************
  GENERAL
********************/

/* font-family: 'Source Code Pro', monospace;
font-family: 'Source Serif Pro', serif; */

* {
  box-sizing: border-box;
}

html {
  font-family: "Source Code Pro", monospace;
  font-size: 62.5%;
  letter-spacing: 1.1;
}

body {
  margin: 0;
  background-color: red;
  color: #333;
}

/*******************
  HEADER
********************/

.header-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3.2rem 0 2.4rem;
}

.logo {
  height: 5rem;
  cursor: pointer;
}

.menu,
.account {
  height: 3.2rem;
  margin: 0.8rem;
}

/*******************
  MEDIA QUERIES
********************/

@media screen and (max-width: 767px) {
  .container {
    margin: 0 16px;
  }

  .logo {
    height: 4rem;
  }
}

@media screen and (min-width: 768px) {
  .container {
    margin: 0 80px;
  }
}

@media screen and (min-width: 1100px) {
  /* body {
    background-color: green;
  } */
}

@media screen and (min-width: 1920px) {
  .container {
    max-width: 1760px;
    margin: 0 auto;
  }
}

.card {
  /* border: 1px solid #333; */
  border-radius: 1rem;
  background-color: #fff;
  justify-self: center;
  align-self: center;
  display: flex;
  justify-content: space-between;
  /* box-shadow: 0 0 15px 15px rgba(0, 0, 0, 0.1); */
  transition: all 0.5s;
}

.card:hover {
  box-shadow: inset 0px 0px 12px 0px rgba(197, 197, 197, 1);
}

/*******************
FORM BOX STYLE
********************/

.inner-box {
  padding: 2.4rem;
  width: 100%;
  position: relative;
}

.text-quote__list {
  color: #333;
  font-size: 2.4rem;
  font-family: "Source Serif Pro", serif;
  font-style: italic;
  margin-top: 0;
  margin-bottom: 0.8rem;
}

.author-quote {
  font-size: 1.4rem;
  font-weight: 300;
}

/*******************
 ICONS
********************/

.icons {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(50%);
  display: flex;
  justify-content: space-between;
  margin-right: 24px;
  grid-gap: 8px;
  gap: 8px;
}

/*******************
 ICONS-EXPANDED
********************/

.icons__expanded {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(50%);
  grid-gap: 8px;
  gap: 8px;
  padding: 0 16px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  grid-gap: 8px;
  gap: 8px;
}

.icon-container-list__expanded {
  height: 4.8rem;
  background-color: #fff;
  border-radius: 100px;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.08);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 240px;
  cursor: pointer;
  transition: border 0.4s;
  border: 1px solid #fff;
  overflow: hidden;
}

.icon-container-list__expanded:first-child {
  position: relative;
}

.icon-container-list__expanded:hover {
  border: 1px solid #3e3e3e;
  background-color: #f3f3f3;
  box-shadow: none;
}

.action-done {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #3e3e3e;
  color: #fff;
  top: -50px;
  width: 100%;
  height: 4.8rem;
  border-radius: 1000px;
  transition: all 0.8s;
}

.action-done svg path {
  fill: #fff;
}

/*******************
 FORM BOX STYLE
********************/

form {
  position: relative;
  width: 100%;
  border-radius: 1rem;
  background-color: #fff;
  padding: 2.4rem 2.4rem 4.8rem 2.4rem;
  height: 34rem;
}

.new-quote_text {
  margin-bottom: 1.6rem;
  width: 100%;
}

.new-quote_text label {
  display: block;
  font-size: 1.6rem;
  margin-bottom: 0.8rem;
  text-transform: uppercase;
}

.new-quote_text textarea {
  height: 12rem;
  width: 100%;
  border: none;
  font-family: "Source Serif Pro", serif;
  border-radius: 10px;
  background-color: transparent;
  font-size: 2.4rem;
  resize: none;
}

.new-quote_author label {
  display: block;
  font-size: 1.1rem;
  margin-bottom: 0.8rem;
}

.new-quote_author input {
  width: 100%;
  border: none;
  font-size: 1.6rem;
  font-family: inherit;
  position: relative;
  border-bottom: 1px solid #3e3e3e;
  padding-bottom: 10px;
}

.author-message {
  font-size: 1.1rem;
}

/*******************
 BUTTONS SMALL
********************/

.new-quote_actions {
  position: absolute;
  display: flex;
  grid-gap: 8px;
  gap: 8px;
  bottom: -24px;
  right: 24px;
}

#btn-add {
  background-color: #3e3e3e;
}

#btn-add svg path {
  fill: #fff;
}

/*******************
 ICON EXPANDED 
********************/

.icons-form__expanded {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  grid-gap: 8px;
  gap: 8px;
  padding: 0 16px;
  bottom: -24px;
  left: 0;
  cursor: pointer;
}

.icon-container-form__expanded {
  border-radius: 1000px;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  border: 1px solid #fff;
  background-color: #fff;
  transition: border 0.4s;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.08);
}

.icon-container-form__expanded:hover {
  border: 1px solid #3e3e3e;
  background-color: #f3f3f3;
  box-shadow: none;
}

.icon-container-form__expanded:last-child {
  background-color: #3e3e3e;
}

.icon-container-form__expanded:last-child:hover {
  background-color: #525252;
  border: 1px solid #fff;
}

#btn-add__expanded {
  flex-grow: 1;
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  color: #fff;

  cursor: pointer;
}

#btn-add__expanded svg path {
  fill: #fff;
}

.cancel-icon__expanded {
}

/*******************
 INVALID CAUSE
********************/

.invalid {
  outline: 2px solid red;
  background-color: rgba(255, 0, 0, 0.048);
}

.invalid:-ms-input-placeholder {
  color: red;
}

.invalid::placeholder {
  color: red;
}

.fixed {
  position: fixed;
  z-index: 10;
  margin-bottom: 0 !important;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
}

.button_fixed {
  background-color: #3e3e3e;
  width: 323px;
  height: 4.8rem;
  border-radius: 1000px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  grid-gap: 8px;
  gap: 8px;
}

.button_fixed span {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-family: "Source Code Pro", sans-serif;
  margin-right: 8px;
}

.button_fixed path {
  fill: #fff;
}

.modal-box {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #f3f3f3;
  z-index: 15;
}

.modal-form {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 94vh;
  width: 95vw;
  background-color: #fff;
  z-index: 20;
  overflow-y: hidden;
  border-radius: 10px 10px 0px 0px;
}

.icons-form__modal {
  display: flex;
  flex-direction: column;
  grid-gap: 8px;
  gap: 8px;
}

.stroke {
  border: 1px solid #3e3e3e;
}

.title-form {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  z-index: 18;
  margin: 10px 10px;
}

.title {
  font-size: 1.6rem;
  text-transform: uppercase;
  margin: 5px auto;
}

.modal-author {
  margin: 260px 0 40px 0;
}

.new-quote__modal label {
  text-transform: none !important;
}

#dismiss-icon {
  background-color: #f3f3f3;
  box-shadow: none;
}

.my-masonry-grid {
  display: flex;
  margin-left: -48px;
}
.my-masonry-grid_column {
  background-clip: padding-box;
  padding-left: 48px;
}

.my-masonry-grid_column > div {
  margin-bottom: 6.4rem;
}

.search-bar {
  margin: 0 auto;
  position: relative;
  padding: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-input {
  width: 96%;
  height: 4.8rem;
  font-size: 1.6rem;
  font-family: inherit;
  font-weight: 600;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #3e3e3e;
}

.search-input:focus {
  outline: none;
}

.search-icon {
  height: 3.2rem;
}

.search-bar__fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #f3f3f3;
  z-index: 8;
  padding-left: 16px;
  padding-right: 16px;
  box-shadow: 0 5px 15px 5px rgba(0, 0, 0, 0.3);
}

/*******************
  LAYOUT & TEXT
********************/

.suggested-quote {
  margin: 3rem 0;
  position: relative;
}

.suggested-flex {
  display: flex;
  justify-content: space-between;
}

.quotemarks {
  position: absolute;
  height: 7.3rem;
  z-index: -2;
}

.text-quote {
  font-family: "Source Serif Pro", serif;
  color: #3e3e3e;
  font-size: 3.2rem;
  font-style: italic;
  margin: 6.3rem 0 0.8rem 0;
}

.author-quote {
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0 0 2.4rem 0;
}

/*******************
  ICONS
********************/

.suggested-icons {
  position: absolute;
  bottom: 0;
  right: 0;
  align-self: flex-end;
  display: flex;
  grid-gap: 8px;
  gap: 8px;
  margin-right: 24px;
}

.save-icon,
.delete-icon {
  cursor: pointer;
}

.icon-container,
.icon-container-suggested__expanded {
  height: 4.8rem;
  width: 4.8rem;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.08);
  cursor: pointer;
}

.icon-container:hover,
.icon-container-suggested__expanded:hover {
  border: 1px solid #3e3e3e;
  background-color: #f3f3f3;
  box-shadow: none;
}

/*******************
 EXPANDED ICONS
********************/

.icon-container-suggested__expanded {
  width: 18rem;
  border-radius: 100px;
  border: 1px solid #fff;
}

.button-label {
  font-size: 1.6rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-right: 8px;
  margin-left: 4px;
}

/*******************
 DISMISSED SUGGESTION
********************/

.blank {
  height: 11.8rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px;
}

.blank p {
  font-size: 1.6rem;
  color: #6e6e6e;
}

.action {
  text-transform: uppercase;
  font-weight: 600;
  color: #6e6e6e;
  margin: 0 8px;
  display: flex;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
}

#reset-btn {
  cursor: pointer;
}

#reset-btn path {
  fill: #6e6e6e;
}
#reset-btn path:hover {
  fill: #3e3e3e;
}

/*******************
 LOADING SUGGESTION
********************/

.loading-screen {
  height: 15.5rem;
  text-align: center;
  margin: 3rem 0;
}

@media screen and (max-width: 767px) {
  .suggested-flex {
    flex-direction: column;
  }

  .suggested-icons {
    position: static;
    position: initial;
  }
  .quotemarks {
    height: 60px;
  }

  .text-quote {
    font-size: 2.4rem;
    margin-top: 4.8rem;
  }

  .icon-container-suggested__expanded {
    width: 120px;
  }

  /*******************
 DISMISSED SUGGESTION
********************/

  .blank {
    padding-top: 2rem;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 0;
    gap: 0;
  }

  .blank p {
    margin: 0;
  }

  .action {
    margin: 0;
  }
}

